import React from 'react';
import { io } from 'socket.io-client';
import toast from 'react-hot-toast';

import { showNotification } from '../Android';

type NotificationType = {
  t: string,
  m: string,
};
let socket:any;
const socketUrl = 'ws://localhost:8888';

const initSocket = () => {
  console.log('WS: init socket');

  const toastLoaderId = toast.loading('Connecting...');
  const storage:string = localStorage.getItem('adresses') || '';
  const address = JSON.parse(storage)[0];

  socket = io(socketUrl, { auth: { address } });

  socket.onAny((event:any, ...args:any) => {
    console.log(event, args);
  });

  socket.on('connect', () => {
    console.log('WS: connected');
    toast.dismiss(toastLoaderId);
    toast.success('Connected');
  });

  socket.on('notify', (data:NotificationType) => {
    toast(<span>{data.t} <br /> {data.m}</span>);
    showNotification(data.t, data.m);
  });
};

const wsSend = (chanel:string, data:object) => {
  socket.emit(chanel, data);
};

export { initSocket, wsSend };
