import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect, Link } from 'react-router-dom';
import toast, { Toaster } from 'react-hot-toast';

import { initSocket, wsSend } from './socket';
import Auth from './pages/Auth';
import Home from './pages/Home';

import './App.scss';

declare const Android: any;

const Test = () => {
  const [err, setErr] = useState('empty');
  const onClick = () => {
    try {
      Android.showToast('Отключают свет!!!', 'Адрес Самарская 28');
    } catch (e) {
      console.log('Android functions not found');
      console.log({ e });
      setErr(e.stack);
    }
  };

  return (
    <main>
      <button type="button" onClick={onClick}> send push </button>
      <hr />
      err: 
      {err}
      <hr />
      <Link to="/"><button type="button"> to home </button></Link>
    </main>
  );
};

function App() {
  const adresses = localStorage.getItem('adresses');

  useEffect(() => {
    initSocket();
  }, []);

  return (
    <>
      <Router basename="/komunation">
        <Switch>
          <Route path="/auth">
            { adresses ? <Redirect to="/" /> : <Auth /> }
          </Route>
          <Route exact path="/" render={() => <Home />} />
          <Route exact path="/test" render={() => <Test />} />
        </Switch>
      </Router>

      <Toaster />
    </>
  );
}

export default App;
